<template>
  <!-- 请求 state  1 -->
  <div class="historyList">
    <div class="left_box">
      <el-form :inline="true" :model="selectForm" class="demo-form-inline">
        <el-form-item label="海外仓承运商">
          <el-select v-model="selectForm.warehouse">
            <el-option
              v-for="(item, i) in carries"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="国家">
          <el-select @change="changeCountry" v-model="selectForm.country">
            <el-option
              v-for="(item, i) in countries"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="订单号">
          <el-select filterable v-model="selectForm.orderid">
            <el-option
              v-for="(item, i) in ordernums"
              :key="i"
              :label="item"
              :value="item"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="订单状态">
          <el-select filterable v-model="selectForm.state">
            <el-option
              v-for="(item, i) in orderstates"
              :key="i"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="query">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="right_box">
      <el-table
        ref="phTable"
        max-height="750"
        border
        :data="historyOrder"
        style="width: 100%"
      >
        <el-table-column
          type="index"
          label="序号"
          width="60"
          align="center"
          :index="indexMethod"
        >
        </el-table-column>
        <el-table-column
          prop="sys_num"
          label="系统单号"
          width="160"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="order_num"
          label="订单号"
          width="160"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="product_code"
          label="产品编码"
          width="100"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="fbm_carrites"
          label="海外仓承运商"
          width="140"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="product_name"
          label="品名"
          width="240"
          align="center"
        >
        </el-table-column>
        <el-table-column prop="sku" label="sku" width="240" align="center">
        </el-table-column>
        <el-table-column prop="country" label="国家" width="100" align="center">
        </el-table-column>
        <el-table-column
          prop="receive_name"
          label="收件人"
          width="100"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="problem_reason"
          label="问题原因"
          width="100"
          align="center"
        >
        </el-table-column>
        <el-table-column
          prop="order_state"
          label="订单状态"
          width="100"
          align="center"
        >
        </el-table-column>
      </el-table>
      <el-pagination
        @current-change="pageChange"
        :current-page.sync="currentPage"
        :page-size="50"
        layout="total, prev, pager, next,jumper"
        :total="totalNum"
      >
      </el-pagination>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      // 分页
      currentPage: 1,
      totalNum: 0,
      selectForm: {
        warehouse: "",
        country: "",
        orderid: "",
        state: 1
      },
      carries: [],
      warehouse_country: [],
      country_order: [],
      countries: [],
      ordernums: [],
      orderstates: [
        {
          label: "未处理",
          value: 0
        },
        {
          label: "已处理",
          value: 1
        }
      ],
      historyOrder: []
    };
  },
  methods: {
    query() {
      this.orderlist();
    },
    indexMethod(index) {
      return index + 1 + 50 * (this.currentPage - 1);
    },
    pageChange(val) {
      this.currentPage = val;
      // 请求数据
      this.orderlist();
    },
    // changeCarries() {
    //   if (this.selectForm.warehouse) {
    //     this.warehouse_country.forEach(i => {
    //       if (i.warehouse === this.selectForm.warehouse) {
    //         this.countries = i.country;
    //       }
    //     });
    //   }
    // },
    changeCountry() {
      if (this.selectForm.country) {
        this.country_order.forEach(i => {
          console.log(i.country, this.selectForm.country);
          if (i.country === this.selectForm.country) {
            this.ordernums = i.orderid || [];
            this.selectForm.orderid =
              this.ordernums.length > 0 ? this.ordernums[0] : "";
          }
        });
      }
    },
    getSelect(cb) {
      // this._url2 = "http://106.52.43.196:8003/";
      let url = "server/order_management/get_warehouse/";
      this.$axios.get(this._url2 + url).then(res => {
        // res.data.warehouse_list 仓库列表
        this.carries = res.data.warehouse_list || [];
        this.selectForm.warehouse =
          this.carries.length > 0 ? this.carries[0] : "";
        // this.warehouse_country = res.data.warehouse_country;
        this.country_order = res.data.country_order || [];
        this.$axios
          .get(this._url2 + "server/personnel_management/get_sign_country/")
          .then(res => {
            this.countries = res.data.data || [];
            this.selectForm.country =
              this.countries.length > 0 ? this.countries[0] : "";
            this.changeCountry();
            if (cb && typeof cb === "function") {
              cb();
            }
          });
      });
    },
    orderlist() {
      let url = "server/order_management/get_bad_order/";
      let params = {};
      for (let key in this.selectForm) {
        if (this.selectForm[key] === "") {
          continue;
        }
        params[key] = this.selectForm[key];
      }
      params["page"] = this.currentPage;
      this.$axios
        .get(this._url2 + url, {
          params
        })
        .then(res => {
          this.historyOrder = res.data.data;
          this.totalNum = res.data.total_num;
          this.$nextTick(() => {
            this.$refs["phTable"].bodyWrapper.scrollTop = 0;
          });
        });
    }
  },
  created() {
    this.getSelect(this.orderlist);
  }
};
</script>
<style>
.historyList .right_box >>> .el-pagination {
  background: rgba(255, 255, 255, 0.9);
}
</style>
<style scoped>
.historyList {
  display: flex;
  flex-direction: row;
  padding-top: 30px;
}
.left_box {
  flex: 0 0 18vw;
  padding: 30px 0px;
  background: rgba(255, 255, 255, 0.9);
}
.right_box {
  flex: 0 0 57vw;
  margin: 0 auto;
}
.historyList .el-form-item:last-child {
  margin: 0 auto;
  width: 100%;
  text-align: center;
}
</style>
<style>
.historyList .el-form--inline .el-form-item__label {
  width: 100px;
}
.historyList .right_box .el-pagination {
  background: #fff;
}
</style>



